/* eslint-disable */
import React, { PureComponent } from 'react';
import contactBg from '../images/contact-bg.jpg';
import $ from 'jquery'

/* Contact us page */
function Contact() {
  return (
    <div>
      <div style={{backgroundImage: `url(${contactBg})`}} className="intro-header">
        <div className="site-heading">
        <h1>Contact us</h1>
        <hr className="small" />
        <span className="subheading">Have questions? I have answers (maybe).</span>
        </div>
      </div>
      <div className="container">
        <p>Fill out the form below to send me a message and I will try to get back to you within 24 hours!</p>
        <ContactForm />
      </div>
    </div>
  )
}

class ContactForm extends PureComponent {
  constructor() {
    super()
    this.handleContact = this.handleContact.bind(this)
  }

  handleContact = (e) => {
    $('#contactForm').submit(false)
    $('#success').text('Thank you. Your message was submitted')
    e.preventDefault()
    e.stopPropagation()
    return false
  }

  render() {
    return (
      <form id="contactForm" onSubmit={this.handleContact}>
            <div className="row control-group">
                <div className="form-group col-xs-12 floating-label-form-group controls">
                    <label>Name</label>
                    <input type="text" className="form-control" placeholder="Name" id="name" required />
                    <p className="help-block text-danger"></p>
                </div>
            </div>
            <div className="row control-group">
                <div className="form-group col-xs-12 floating-label-form-group controls">
                    <label>Email Address</label>
                    <input type="email" className="form-control" 
                        id="email" 
                        required  />
                    <p className="help-block text-danger"></p>
                </div>
            </div>
            <div className="row control-group">
                <div className="form-group col-xs-12 floating-label-form-group controls">
                    <label>Phone Number</label>
                    <input type="tel" className="form-control" placeholder="Phone Number (xxxx-xxx-xxxx)" id="phone" required pattern="^\d{4}-\d{3}+\w-\d{4}$" />
                    <p className="help-block text-danger"></p>
                </div>
            </div>
            <div className="row control-group">
                <div className="form-group col-xs-12 floating-label-form-group controls">
                    <label>Message</label>
                    <textarea rows="5" className="form-control" placeholder="Message" id="message" required data-validation-required-message="Please enter a message." ></textarea>
                    <p className="help-block text-danger"></p>
                </div>
            </div>
            <div id="success"></div>
            <div className="row">
                <div className="form-group col-xs-12">
                    <button type="submit" className="btn btn-default">Send</button>
                </div>
            </div>
        </form>
    )
  }
}

export default Contact



